import React from "react";
import { Pie } from "@ant-design/charts";
import "./App.css";

var data = [
  {
    type: "Tnb Teams",
    value: 27,
  },
  {
    type: "Project Teams",
    value: 25,
  },
  {
    type: "Completed Tasks",
    value: 18,
  },
  {
    type: "Accounts on Tnb and Project Teams ",
    value: 15,
  },
  {
    type: "Non Team Members",
    value: 10,
  },
];

